exports.push([module.id, "/**\n * All the units used below are pixels due to variable name conflict in widget and dashboard\n **/\n.emoji-dialog[data-v-6e0d74b2] {\n  box-shadow: 0 8px 16px rgba(50, 50, 93, 0.08), 0 4px 12px rgba(0, 0, 0, 0.07);\n  border-radius: 8px;\n  background: #fff;\n  border-radius: 8px;\n  box-sizing: content-box;\n  position: absolute;\n  right: 0;\n  top: -220px;\n  width: 320px;\n  z-index: 1;\n}\n.emoji-dialog[data-v-6e0d74b2]::before {\n  display: block;\n  height: 0;\n  width: 0;\n  content: \"\";\n  border-left: 12px solid transparent;\n  border-right: 12px solid transparent;\n  border-top: 12px solid #fff;\n  bottom: -12px;\n  position: absolute;\n  right: 20px;\n}\n.emoji-dialog .emoji--item[data-v-6e0d74b2] {\n  cursor: pointer;\n  background: transparent;\n  border: 0;\n  font-size: 18px;\n  margin: 0;\n  padding: 0;\n}\n.emoji-dialog .emoji--row[data-v-6e0d74b2] {\n  box-sizing: border-box;\n  height: 180px;\n  overflow-y: auto;\n  padding: 4px 16px;\n}\n.emoji-dialog .emoji--row .emoji--item[data-v-6e0d74b2] {\n  float: left;\n  margin: 4px;\n  line-height: 1.5;\n}\n.emoji-dialog .emoji-category--title[data-v-6e0d74b2] {\n  color: #1f2d3d;\n  font-size: 14px;\n  font-weight: 500;\n  line-height: 1.5;\n  margin: 0;\n  text-transform: capitalize;\n}\n.emoji-dialog--header[data-v-6e0d74b2] {\n  background-color: #3c4858;\n  border-top-left-radius: 8px;\n  border-top-right-radius: 8px;\n  padding: 0 4px;\n}\n.emoji-dialog--header ul[data-v-6e0d74b2] {\n  display: flex;\n  list-style: none;\n  overflow: auto;\n  margin: 0;\n  padding: 4px 0 0;\n}\n.emoji-dialog--header ul > li[data-v-6e0d74b2] {\n  align-items: center;\n  cursor: pointer;\n  display: flex;\n  height: 24px;\n  justify-content: center;\n  padding: 4px 8px;\n}\n.emoji-dialog--header ul > .active[data-v-6e0d74b2] {\n  background: #fff;\n  border-top-left-radius: 4px;\n  border-top-right-radius: 4px;\n}", "",{"version":3,"sources":["/Users/s00d/packeges/chatwoot/app/javas