ion: absolute;\n    right: $space-two;\n  }\n\n  .emoji--item {\n    cursor: pointer;\n    background: transparent;\n    border: 0;\n    font-size: $font-size-medium;\n    margin: 0;\n    padding: 0;\n  }\n\n  .emoji--row {\n    box-sizing: border-box;\n    height: $space-one * 18;\n    overflow-y: auto;\n    padding: $space-smaller $space-normal;\n\n    .emoji--item {\n      float: left;\n      margin: $space-smaller;\n      line-height: 1.5;\n    }\n  }\n\n  .emoji-category--title {\n    color: $color-heading;\n    font-size: $font-size-small;\n    font-weight: 500;\n    line-height: 1.5;\n    margin: 0;\n    text-transform: capitalize;\n  }\n}\n\n.emoji-dialog--header {\n  background-color: $color-body;\n  border-top-left-radius: $space-small;\n  border-top-right-radius: $space-small;\n  padding: 0 $space-smaller;\n\n  ul {\n    display: flex;\n    list-style: none;\n    overflow: auto;\n    margin: 0;\n    padding: $space-smaller 0 0;\n\n    > li {\n      align-items: center;\n      cursor: pointer;\n      display: flex;\n      height: 2.4 * $space-one;\n      justify-content: center;\n      padding: $space-smaller $space-small;\n    }\n\n    > .active {\n      background: $color-white;\n      border-top-left-radius: $space-smaller;\n      border-top-right-radius: $space-smaller;\n    }\n  }\n}\n","// scss-lint:disable PseudoElement SpaceBeforeBrace VendorPrefix\n$shadow-color-1: rgba(50, 50, 93, 0.08);\n$shadow-color-2: rgba(0, 0, 0, 0.07);\n$shadow-color-3: rgba(50, 50, 93, .08);\n$shadow-color-4: rgba(0, 0, 0, .05);\n\n$color-shadow-medium: rgba(50, 50, 93, 0.08);\n$color-shadow-light: rgba(50, 50, 93, 0.04);\n$color-shadow-large: rgba(50, 50, 93, 0.25);\n$color-shadow-outline: rgba(66, 153, 225, 0.5);\n\n@mixin normal-shadow {\n  box-shadow: 0 $space-small $space-normal $shadow-color-1, 0 $space-smaller $space-slab $shadow-color-2;\n}\n\n@mixin light-shadow {\n  box-shadow: 0 $space-smaller 6px $shadow-color-3, 0 1px 3px $shadow-color-4;\n}\n\n@mixin placeholder {\n  &::-webkit-input-placeholder {\n    @content\n  }\n\n  &:-moz-placeholder {\n    @content\n  }\n\n  &::-moz-placeholder {\n    @content\n  }\n\n  &:-ms-input-placeholder {\n    @content\n  }\n}\n\n@mixin shadow {\n  box-shadow: 0 1px 10px 2px $color-shadow-medium,\n    0 1px 5px 1px $color-shadow-light;\n}\n\n\n@mixin shadow-medium {\n  box-shadow: 0 4px 24px 4px $color-shadow-medium,\n    0 2px 16px 2px $color-shadow-light;\n}\n\n\n@mixin shadow-large {\n  box-shadow: 0 10px 15px -16px $color-shadow-medium,\n    0 4px 6px -8px $color-shadow-light;\n}\n\n\n@mixin shadow-big {\n  box-shadow: 0 20px 25px -20px $color-shadow-medium,\n    0 10px 10px -10px $color-shadow-light;\n}\n\n\n@mixin shadow-mega {\n  box-shadow: 0 25px 50px -12px $color-shadow-big;\n}\n\n@mixin shadow-inner {\n  box-shadow: inset 0 2px 4px 0 $color-shadow-light;\n}\n\n@mixin shadow-outline {\n  box-shadow: 0 0 0 3px $color-shadow-outline;\n}\n\n@mixin shadow-none {\n  box-shadow: none;\n}\n\n@mixin button-size {\n  min-height: $space-large;\n  min-width: $space-large;\n}\n","@import '~dashboard/assets/scss/variables';\n@import '~widget/assets/scss/mixins';\n\n$spinner-before-border-color: rgba(255, 255, 255, 0.7);\n\n//borders\n@mixin border-nil() {\n  border-color: transparent;\n  border: 0;\n}\n\n@mixin thin-border($color) {\n  border: 1px solid $color;\n}\n\n@mixin custom-border-bottom($size, $color) {\n  border-bottom: $size solid $color;\n}\n\n@mixin custom-border-top($size, $color) {\n  border-top: $size solid $color;\n}\n\n@mixin border-normal() {\n  border: 1px solid $color-border;\n}\n\n@mixin border-normal-left() {\n  border-left: 1px solid $color-border;\n}\n\n@mixin border-normal-top() {\n  border-top: 1px solid $color-border;\n}\n\n@mixin border-normal-right() {\n  border-right: 1px solid $color-border;\n}\n\n@mixin border-normal-bottom() {\n  border-bottom: 1px solid $color-border;\n}\n\n@mixin border-light() {\n  border: 1px solid $color-border-light;\n}\n\n@mixin border-light-left() {\n  border-left: 1px solid $color-border-light;\n}\n\n@mixin border-light-top() {\n  border-top: 1px solid $color-border-light;\n}\n\n@mixin border-light-right() {\n  border-right: 1px solid $color-border-light;\n}\n\n@mixin border-light-bottom() {\n  border-bottom: 1px solid $color-border-light;\n}\n\n// background\n@mixin background-gray() {\n  background: $color-background;\n}\n\n@mixin background-light() {\n  background: $color-background-light;\n}\n\n@mixin background-white() {\n  background: $color-white;\n}\n\n// input form\n@mixin ghost-input() {\n  box-shadow: none;\n  border-color: transparent;\n\n  &:active,\n  &:hover,\n  &:focus {\n    border-color: transparent;\n    box-shadow: none;\n  }\n}\n\n// flex-layout\n@mixin space-between() {\n  display: flex;\n  justify-content: space-between;\n}\n\n@mixin space-between-column() {\n  @include space-between;\n  flex-direction: column;\n}\n\n@mixin space-between-row() {\n  @include space-between;\n  flex-direction: row;\n}\n\n@mixin flex-shrink() {\n  flex: 0 0 auto;\n  max-width: 100%;\n}\n\n@mixin flex-weight($value) {\n  // Grab flex-grow for older browsers.\n  $flex-grow: nth($value, 1);\n\n  // 2009\n  @include prefixer(box-flex, $flex-grow, webkit moz spec);\n\n  // 2011 (IE 10), 2012\n  @include prefixer(flex, $value, webkit moz ms spec);\n}\n\n// full height\n@mixin full-height() {\n  height: 100%;\n}\n\n@mixin round-corner() {\n  border-radius: 1000px;\n}\n\n@mixin scroll-on-hover() {\n  overflow: hidden;\n\n  &:hover {\n    overflow-y: auto;\n  }\n}\n\n\n@mixin horizontal-scroll() {\n  overflow-y: auto;\n}\n\n@mixin elegant-card() {\n  @include normal-shadow;\n  border-radius: $space-small;\n}\n\n@mixin color-spinner() {\n  @keyframes spinner {\n    to {\n      transform: rotate(360deg);\n    }\n  }\n\n  &::before {\n    animation: spinner .9s linear infinite;\n    border: 2px solid $spinner-before-border-color;\n    border-radius: 50%;\n    border-top-color: lighten($color-woot, 10%);\n    box-sizing: border-box;\n    content: '';\n    height: $space-medium;\n    left: 50%;\n    margin-left: -$space-one;\n    margin-top: -$space-one;\n    position: absolute;\n    top: 50%;\n    width: $space-medium;\n  }\n}\n\n// --------------------------------------------------------\n// arrows\n// --------------------------------------------------------\n// $direction: top, left, right, bottom, top-left, top-right, bottom-left, bottom-right\n// $color: hex, rgb or rbga\n// $size: px or em\n// @example\n// .element{\n//     @include arrow(top, #000, 50px);\n// }\n@mixin arrow($direction, $color, $size) {\n  display: block;\n  height: 0;\n  width: 0;\n  content: '';\n\n  @if $direction == 'top' {\n    border-bottom: $size solid $color;\n    border-left: $size solid transparent;\n    border-right: $size solid transparent;\n  }\n\n  @else if $direction == 'right' {\n    border-bottom: $size solid transparent;\n    border-left: $size solid $color;\n    border-top: $size solid transparent;\n  }\n\n  @else if $direction == 'bottom' {\n    border-left: $size solid transparent;\n    border-right: $size solid transparent;\n    border-top: $size solid $color;\n  }\n\n  @else if $direction == 'left' {\n    border-bottom: $size solid transparent;\n    border-right: $size solid $color;\n    border-top: $size solid transparent;\n  }\n\n  @else if $direction == 'top-left' {\n    border-right: $size solid transparent;\n    border-top: $size solid $color;\n  }\n\n  @else if $direction == 'top-right' {\n    border-left: $size solid transparent;\n    border-top: $size solid $color;\n  }\n\n  @else if $direction == 'bottom-left' {\n    border-bottom: $size solid $color;\n    border-right: $size solid transparent;\n  }\n\n  @else if $direction == 'bottom-right' {\n    border-bottom: $size solid $color;\n    border-left: $size solid transparent;\n  }\n}\n\n@mixin text-ellipsis {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n\n@mixin three-column-grid($column-one-width: 25.6rem,\n  $column-three-width: 25.6rem) {\n  width: 100%;\n  height: 100%;\n  display: grid;\n  grid-template-columns: minmax($column-one-width, 6fr) 10fr minmax($column-three-width, 6fr);\n}\n","// Font sizes\n$font-size-nano: 0.8rem;\n$font-size-micro: 1.0rem;\n$font-size-mini: 1.1rem;\n$font-size-small: 1.2rem;\n$font-size-default: 1.4rem;\n$font-size-medium: 1.6rem;\n$font-size-large: 2.0rem;\n$font-size-big: 2.2rem;\n$font-size-bigger: 2.8rem;\n$font-size-mega: 3.0rem;\n$font-size-giga: 3.5rem;\n\n// spaces\n$zero: 0;\n$space-micro: 0.2rem;\n$space-smaller: 0.3rem;\n$space-small: 0.7rem;\n$space-one: 0.9rem;\n$space-slab: 1.1rem;\n$space-normal: 1.5rem;\n$space-two: 1.9rem;\n$space-medium: 2.3rem;\n$space-large: 3.1rem;\n$space-larger: 4.7rem;\n$space-jumbo: 6.3rem;\n$space-mega: 10.0rem;\n\n// font-weight\n$font-weight-feather: 100;\n$font-weight-light: 300;\n$font-weight-normal: 400;\n$font-weight-medium: 500;\n$font-weight-bold: 600;\n$font-weight-black: 700;\n\n//Navbar\n$nav-bar-width: 23rem;\n$header-height: 5.6rem;\n\n$woot-logo-padding: $space-large $space-two;\n\n// Colors\n$color-woot: #1f93ff;\n$color-gray: #6e6f73;\n$color-light-gray: #999a9b;\n\n$color-border: var(--s-75);\n$color-border-light: var(--s-50);\n$color-border-dark: var(--s-100);\n\n$color-background: var(--s-50);\n$color-background-light: var(--s-25);\n\n$color-white: #fff;\n$color-body: #3c4858;\n$color-heading: #1f2d3d;\n$color-extra-light-blue: #f5f7f9;\n\n$primary-color: $color-woot;\n$secondary-color: #5d7592;\n$success-color: #44ce4b;\n$warning-color: #ffc532;\n$alert-color: #ff382d;\n\n$masked-bg: rgba(0, 0, 0, .4);\n\n// Color-palettes\n\n$color-primary-light: #c7e3ff;\n$color-primary-dark: darken($color-woot, 20%);\n\n// Thumbnail\n$thumbnail-radius: 4rem;\n\n// chat-header\n$conv-header-height: 4rem;\n\n// Inbox List\n\n$inbox-thumb-size: 4.8rem;\n\n\n// Spinner\n$spinkit-spinner-color: $color-white !default;\n$spinkit-spinner-margin: 0 0 0 1.6rem !default;\n$spinkit-size: 1.6rem !default;\n\n// Snackbar default\n$woot-snackbar-bg: #323232;\n$woot-snackbar-button: #ffeb3b;\n\n$swift-ease-out-duration: .4s !default;\n$swift-ease-out-function: cubic-bezier(0.37, 0, 0.63, 1) !default;\n$swift-ease-out: all $swift-ease-out-duration $swift-ease-out-function !default;\n\n// Ionicons\n$ionicons-font-path: '~ionicons/fonts';\n\n// Transitions\n$transition-ease-in: all 0.250s ease-in;\n"]}]);
